.catalog-content{
    width: calc(952 / 1280 * 100%);
    margin-bottom: 68px;
    &__filtres{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 32px;
    }
    &__cards{
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(296px, 1fr));
      gap: 32px;
      margin-bottom: 33px;
    }
    &__top{
      display: flex;
      margin-bottom: 24px;
    }
    &__list{
        display: flex;
        flex-wrap: wrap;
    }
    &__item{
      width: fit-content;
      padding: 0 15px 15px 0;
    }
    &__title{
      font-weight: 400;
      font-size: 24px;
      color: $colorBlack;
      margin-right: 17px;
    }
    &__button{
      border: none;
      border-radius: 3px;
      padding: 7px 27px 6px 10px;
      background-image: url('../img/close-filtres.svg');
      background-repeat: no-repeat;
      background-position: top 3px right 4px;
      color: $colorGreyShade;
      font-size: 14px;
      text-align: left;
      cursor: pointer;
      width: 100%;
      &--lime{
        background-color: $colorLime;
      }
      &--bone{
        background-color: $colorBone;
      }
      &--pink-violet{
        background-color: $colorFocuselement;
      }&--grey-light{
        background-color: $colorGreyLight;
      }
    }
    .card--rating{
      padding-bottom: 32px;
    }
  }
  .paginations{
    display: flex;
    justify-content: center;
    &__list{
      display: flex;
      align-items: center;
    }
    &__item:not(:last-child){
      margin-right: 16px;
    }
    &__btn{
      &.active{
        background-color: $colorPrimaryShade;
        color: $colorWhite;
        -webkit-box-shadow: 0px 0px 0px 2px rgba(112, 51, 172, 1) inset;
        -moz-box-shadow: 0px 0px 0px 2px rgba(112, 51, 172, 1) inset;
        box-shadow: 0px 0px 0px 2px rgba(112, 51, 172, 1) inset;
      }
      cursor: pointer;
      font-weight: 600;
      font-size: 16px;
      color: $colorPrimary;
      background-color: transparent;
      border-radius: 10px;
      padding: 8px 11px;
      transition: background 0.2s linear,
      color 0.2s linear,
      box-shadow 0.2s linear;
      outline: none;
      border: none;
      -webkit-box-shadow: 0px 0px 0px 2px #A65CF0 inset;
        -moz-box-shadow: 0px 0px 0px 2px #A65CF0 inset;
        box-shadow: 0px 0px 0px 2px #A65CF0 inset;
      &:hover{
        background-color: $colorPrimary;
        color: $colorWhite;
      }
      &:focus{
        background-color: $colorPrimaryLight;
        color: $colorWhite;
        -webkit-box-shadow: 0px 0px 0px 2px #A65CF0 inset;
        -moz-box-shadow: 0px 0px 0px 2px #A65CF0 inset;
        box-shadow: 0px 0px 0px 2px #A65CF0 inset;
      }
      &:active{
        background-color: $colorPrimaryShade;
        color: $colorWhite;
        -webkit-box-shadow: 0px 0px 0px 2px rgba(112, 51, 172, 1) inset;
        -moz-box-shadow: 0px 0px 0px 2px rgba(112, 51, 172, 1) inset;
        box-shadow: 0px 0px 0px 2px rgba(112, 51, 172, 1) inset;
      }
    }
  }
  .catalog-content-filtres{
    display: none;
    &__text{
      font-size: 14px;
      color: $colorBlack;
    }
    &__list{
      display: grid;
      grid-template-columns: repeat(4,1fr);
      grid-gap: 32px;
      width: calc(843 / 952 * 100%);
    }
    &__item{
      position: relative;
      &--zindex1{
        z-index: 1;
      }
      &--zindex2{
        z-index: 2;
      }
    }
    &__button{
      border-radius: 10px;
      padding: 9px 15px;
      background-color: $colorGreyLight;
      border: none;
      font-size: 16px;
      color: $colorBlack;
      background-image: url('../img/filtres-arrow-down.svg');
      background-repeat: no-repeat;
      background-position: top 17px right 15px;
      width: 100%;
      text-align: left;
    }
  }
  .js-catalog-filtres-hidden{
    display: none;
  }