
.breadcrumbs{
    font-size: 14px;
    color: $colorGrey;
    &__catalog{
      padding-bottom: 32px;
    }
    &__list{
      display: flex;
    }
    &__item:not(:last-child){
      margin-right: 15px;
    }
    &__link{
      position: relative;
      transition: color 0.2s linear;
      outline: none;
      &:before{
        content: '';
        position: absolute;
        top: -3px;
        right: -3px;
        bottom: -3px;
        left: -3px;
        background-color: $colorPrimaryLight;
        transform: scale(0);
        transition: transform 0.2s linear;
      }
      &::after{
        content: '/';
        position: absolute;
        width: 5;
        height: 10px;
        top: 2px;
        right: -9px;
  
      }
      &:hover{
        color: $colorPrimary;
      }
      &:focus{
        color: $colorPrimaryShade;
      }
      &:focus:not(:active):before{
        transform: scale(1);
      }
      &:active{
        color: $colorPrimaryShade;
      }
    }
    &__link-text{
      position: relative;
      &--active{
        color: $colorPrimaryShade;
      }
    }
  }