.catalog-filtres{
    width: 296px;
    &--margin{
      margin-right: 32px;
    }
    &__filtres{
      margin-bottom: 31px;
    }
    &__title{
      color: $colorBlack;
      font-size: 18px;
      margin-bottom: 23px;
    }
    &__category--category{
      margin-bottom: 26px;
    }
    &__category--sale{
      margin-bottom: 26px;
    }
    &__category--sale{
      margin-bottom: 28px;
    }
    &__chekbox-label{
      color: $colorGreyShade;
      font-size: 16px;
      display: flex;
      align-items: center;
    }
    &__list{
      margin-bottom: 10px;
      &--price{
        margin-bottom: 36px;
      }
    }
    &__item:first-child{
      font-weight: 600;
      font-size: 16px;
      color: $colorGreyShade;
    }
    &__item:not(:last-child){
      margin-bottom: 10px;
    }
    &__item--title-price{
      margin-bottom: 7px !important;
    }
    &__link{
      position: relative;
      color: $colorPrimary;
      border-bottom: 1px solid transparent;
      outline: none;
      &-text{
        position: relative;
      }
      &::before{
        content: '';
        position: absolute;
        top: -3px;
        right: -3px;
        left: -3px;
        bottom: -3px;
        background-color: $colorPrimaryLight;
        transform: scale(0);
        transition: transform 0.2s linear;
      }
      &:hover{
        border-bottom-color: $colorPrimary;
      }
      &:focus{
        color: $colorWhite;
      }
      &:focus:not(:active):before{
        transform: scale(1);
      }
      &:active{
        color: $colorPrimaryShade;
        border-bottom-color: $colorPrimaryShade;
      }
    }
    /*стили для позунка начало*/
    &__inputs{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      justify-content: space-between;
    }
    &__text{
      font-size: 16px;
      color: $colorGreyShade;
      margin-right: 9px;
    }
    &__label{
      display: flex;
      align-items: center;
    }
    &__input{
      border: 1px solid $colorGrey;
      border-radius: 2px;
      width: 86px;
      height: 30px; 
      padding: 5px 13px;
      outline: none;
      &:hover{
        cursor: pointer;
        border-color: $colorPrimary;
      }
      &:focus:not(:active){
        border-color: $colorPrimary;
        background-color: $colorPrimaryLight;
      }
      &:active{
        border-color: $colorPrimaryShade;
      }
    }
    &__progress{
      height: 3px;
      border: none;
      border-radius: 3px;
      background-color: $colorMdGrey;
      .noUi-connect{
        background-color: $colorPrimary;
      }
      .noUi-handle:after,
      .noUi-handle:before{
        display: none;
      }
      .noUi-touch-area{
        background-image: url('../img/range-img.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
      .noUi-handle{
        width: 13px;
        height: 13px;
        background-color: transparent;
        border: none;
        box-shadow: none;
        top: 1px;
        right: -6px;
      }
      .noUi-handle-lower{
        right: -7px;
      }
    }
    /*стили для позунка конец*/
    .checkbox-custom{
      width: 20px;
      height: 20px;
      margin-right: 12px;
    }
    .catalog-filtres__item:not(:last-child) {
      margin-bottom: 12px;
    }
  }
  .new-collection{
    &__img{
      display: block;
      width: 100%;
      height: auto;
    }
    &__body{
      background-color: $colorBlack;
      border-radius: 0 0 10px 10px;
      padding: 33px 30px 33px 29px;
    }
    &__title{
      font-family: "PlayfairDisplay";
      color: $colorGold;
      font-weight: 900;
      font-size: 24px;
      margin-bottom: 2px;
    }
    &__subtitle{
      font-size: 16px;
      color: $colorGrey;
      max-width: 170px;
      margin-bottom: 15px;
      line-height: 1.3;
    }
    &__link{
      font-weight: 700;
      font-size: 16px;
      color: $colorPrimary;
      background-image: url(../img/catalog-link.svg);
      background-repeat: no-repeat;
      background-position: center right;
      padding-right: 30px;
      transform: background 0.2s linear,
      color 0.2s linear;
      position: relative;
      outline: none;
      &::before{
        content: "";
      position: absolute;
      width: 72%;
      height: 2px;
      display: block;
      background-color: #A65CF0;
      bottom: -2px;
      transform: scale(0);
      transition: transform 0.2s linear, background 0.2s linear;
      }
      &:hover{
        background-image: url(../img/catalog-link-hover.svg);
      }
      &:focus{
        background-image: url(../img/catalog-link-focus.svg);
      }
      &:active{
        background-image: url(../img/catalog-link-active.svg);
        color: #7033AC;
      }
      &:hover::before,
      &:focus::before,
      &:active::before {
        transform: scale(1);
    }
    }
  }
  .js-catalog-filter-none{
    display: none;
  }