.card{
    padding: 30px;
    background-color: $colorGreyLight;
    border-radius: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    position: relative;
    &__link-overlay{
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
    }
    &--offers{
      min-height: 511px;
    }
    &--small{
      padding-top: 72px;
      &-width{
        width: calc(296 / 1280 * 100%);
      }
    }
    &--md{
      padding-top: 64px;
    }
    &--big{
      padding-top: 37px;
      &-width{
        width: calc(624 / 1280 * 100%);
      }
      &-two{
        padding-top: 59px;
      }
    }
    &--rating{
      padding: 33px 15px;
    }
    &__img{
      display: block;
      max-width: 100%;
      margin: 0 auto;
      margin-bottom: 17px;
    }
    &__title{
      font-weight: 400;
      font-size: 24px;
      color: $colorBlack;
      margin-bottom: 23px;
      line-height: 1.3;
      &--rating{
        padding-left: 15px;
      }
    }
    &__price{
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 10px;
      span:not(:last-child){
        margin-right: 30px;
      }
      &--rating{
        padding-left: 15px;
        margin-bottom: 22px;
      }
      &-actual{
        font-size: 18px;
        color: $colorSecondary;
        &--rating{
          color: $colorGreyShade;
        }
      }
      &-old{
        font-size: 16px;
        color: $colorGrey;
        text-decoration: line-through;
        padding-top: 2px;
      }
    }
    &__shield{
      position: absolute;
      top: 30px;
      left: 0;
      background-color: $colorSecondary;
      padding: 9px 29px 9px 19px;
      clip-path: polygon(90% 0, 100% 49%, 90% 100%, 0 100%, 0 0);
      font-weight: 700;
      font-size: 14px;
      color: $colorWhite;
    }
    &__btn{
      margin-top: auto;
      width: fit-content;
      position: relative;
      z-index: 5;
      &--rating{
        margin-left: 15px;
      }
    }
    &__wrapper{
      display: flex;
    }
    &__img-absolute{
      width: calc(470 / 1280 * 100%);
      height: auto;
      position: absolute;
      right: 30px;
      bottom: 30px;
    }
    &__stars{
      color: $colorSecondary;
      font-size: 16px;
      padding-left: 15px;
      margin-bottom: 10px;
      &--r94{
        margin-bottom: 59px;
      }
      &--t75{
        margin-bottom: 40px;
      }
      &--d31{
        margin-bottom: 38px;
      }
      &--y68{
        margin-bottom: 16px;
      }
      &--w95{
        margin-bottom: 20px;
      }
      &--g41{
        margin-bottom: 23px;
      }
      &--v93{
        margin-bottom: 7px;
      }
      &--s99{
        margin-bottom: 13px;
      }
      &--h64{
        margin-bottom: 12px;
      }
      &--r43{
        margin-bottom: 32px;
      }
      &--u58{
        margin-bottom: 43px;
      }
      &--f41{
        margin-bottom: 28px;
      }
      &--r85{
        margin-bottom: 34px;
      }
      &--s44{
        margin-bottom: 36px;
      }
  
      svg{
        width: 15px;
        height: 15px;
        margin-right: 4px;
      }
    }
  }
  .js-card-rating-none{
    display: none;
  }