.mobile-menu{
    position: absolute;
    top: 105px;
    left: 0;
    right: 0;
    border-top: 1px solid $colorGrey;
    background-color: white;
    padding-top: 34px;
    padding-bottom: 60px;
    z-index: 101;
    background-image: url('../img/mobile-menu-img.png');
    background-repeat: no-repeat;
    background-position: top 119px right 70px;
    transition: transform 0.3s linear;
    &__button{
      margin-bottom: 34px;
    }
    &__lists{
      display: flex;
    }
    &__list:not(:last-child){
      margin-right: 64px;
    }
    &__item:not(:last-child){
      margin-bottom: 41px;
    }
    &__link{
      font-size: 16px;
      color: $colorBlack;
      &:active{
        color: $colorPrimaryShade;
      }
    }
  }
  .close-button{
    width: 24px;
    height: 24px;
    border: none;
    background-color: transparent;
    outline: none;
    position: relative;
    cursor: pointer;
    &__line{
      display: block;
      width: 100%;
      height: 3px;
      border-radius: 4px;
      background-color: $colorPrimary;
      position: absolute;
      &--one{
        transform:rotate(45deg);
      }
      &--two{
        transform:rotate(-45deg);
      }
    }
  }
  .js-mobile-menu-none{
    transform: translateX(-100%);
  }
  